} 

.preloader{
  position:fixed;
  background-color: #000000;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
 display: flex;
 align-items: center;
 margin: auto;
 text-align: center;
 justify-content: center;
}

